import React, {useState} from 'react';
import NavBar from "./components/NavBar";
import cover from "./images/wallpaper.jpg";
import Dashboard from "./components/Dashboard";
import Forecast from "./components/Forecast";
import Options from "./components/Options";
import { Box } from '@material-ui/core';

const useDeg = () => {
  const [degUnit, setDegUnit] = useState('F');
  const handleDegF = () => setDegUnit('F');
  const handleDegC = () => setDegUnit('C');
  return [degUnit, handleDegC, handleDegF];
}

const useOptions = () => {
  const [open, setOpen] = useState(false);
  const [wallpaper, setWallpaper] = useState(cover);
  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);
  return [open, handleOpen, handleClose, wallpaper, setWallpaper];
}

function App() {
  
  const [degUnit, handleDegC, handleDegF] = useDeg();
  const [open, handleOpen, handleClose, wallpaper, setWallpaper] = useOptions();
    
  return (
    <Box class='h-screen  w-100 break-words flex flex-col overflow-hidden' style={{ backgroundImage: `url(${wallpaper})`, backgroundSize: 'cover' }}>
      <NavBar handleOpen={handleOpen}/>
      <Box class='mt-10 overflow-y-auto'>
      <Dashboard degUnit={degUnit}/>
      <Forecast  degUnit={degUnit} />
      {/* <Options open={open} setWallPaper={setWallpaper} handleClose={handleClose} handleDegC={handleDegC} handleDegF={handleDegF}/> */}
      </Box>
    </Box>
  );
}

export default App;
