
import { Typography } from "@material-ui/core";


const data = [1];

const Item = () => (
    <div className=''>
        ccc
    </div>
)

function Forecast() {
    return (
        <div className='mt-8  p-4 text-white'>
            <Typography variant='p' className='inline-block text-3xl font-medium bg-black p-2 border-4 border-white rounded-lg'>Forecast</Typography>

            <div className='flex flex-wrap mt-6'>
                {data.map(item => <Item />)}
            </div>

        </div>
    )
}


export default Forecast;