
import { AppBar, IconButton, Typography, TextField, InputAdornment, Button, Box } from "@material-ui/core";
import SearchIcon from '@material-ui/icons/Search';
import MenuIcon from '@material-ui/icons/Menu';
import $ from 'jquery';


function NavBar({ handleOpen }) {
    const handleMenu = () => {
        if ($('#menu').hasClass('hidden')) {
            $('#menu').removeClass('hidden')
            $('#menu').addClass('flex')
        }
            
        else {
            $('#menu').addClass('hidden')
            $('#menu').removeClass('flex')
        }
    }
    return (
        <AppBar position='static'>
            <Box className='flex  w-full'>
            <div className=' flex flex-col lg:flex-row  p-2 lg:items-center bg-opacity-900 text-whit flex-grow'>
                <div className='flex items-center'>
                <div className='hidden lg:block'><IconButton color='inherit' onClick={handleMenu}><MenuIcon /></IconButton></div>
                    <Typography variant='h6'>API Weather App</Typography>
                </div>
                <div id='menu' className='hidden  lg:flex bg-red-90 items-center sm:justify-center flex-col-reverse sm:flex-row flex-grow sm:space-x-2 space-y-2
                lg:space-y-0'>
                    <TextField margin='dense' size='small' type='search' placeholder='Enter the Name of a City'
                        InputProps={{
                            startAdornment: (
                              <InputAdornment position="start">
                                <SearchIcon />
                              </InputAdornment>
                            ),
                          }}
                        className='w-11/12 sm:w-7/12 lg:w-6/12 xl:w-5/12 bg-white text-black rounded-xl' />
                    <div className='flex space-x-2 '>
                        <Button size='small' variant="outlined" className='h-10 lg:self-center'>Search</Button>
                        <Button onClick={handleOpen} size='small' variant='outlined' color='error' className='h-10 lg:self-center'>Options</Button>
                    </div>
                </div>
                

                </div>
                <div className='lg:hidden text-white self-start ml-auto mt-1'><IconButton color='inherit' onClick={handleMenu}><MenuIcon /></IconButton></div>
                </Box>
        </AppBar>
    )
};


export default NavBar;